body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  margin: 0;
  padding: 0;
  background-color: rgb(30, 41, 59);
}

.App {
  text-align: center;
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  width: 95%;
  display: block;
  margin: auto;
  word-wrap: break-word;
}

.btn {
  margin-top: 5px;
  margin-bottom: 10px;
}

.form {
  color: rgb(138, 138, 138);
}

.form input {
  margin-top: 10px;
  margin-right: 10px;
  color: white;
}

.form input[type="date"] {
  height: 40px;
  width: 130px;
  margin-left: 10px;
}

.form input[type="checkbox"] {
  margin-right: 3px;
  margin-left: 5px;
}

.prior {
  color: rgb(255, 0, 0);
}

.nonprior {
  color: #555;
}

span {
  font-weight: normal;
  font-size: 16px;
  color: #bbb;
}

p {
  font-size: 24px;
  font-weight: bold;
}

.todo {
  border: 1px #999 solid;
  margin-bottom: 4px;
  padding: 5px;
}

.finished button {
  margin-left: 5px;
}

